(function () {
'use strict';

function slidersInit() {

    $('.hero__slider-wrapper').owlCarousel({
        items: 1,
        nav: true,
        dots: true,
        loop: true,
        responsive: {
            0: {
                items: 1
            }
        }
    });

    $('.toy__companies-wrapper').owlCarousel({
        nav: true,
        items: 7,
        margin: 60,
        loop: true,
        autoplay: true,
        autoplaySpeed: 300,
        responsive: {
            0: {
                items: 2
            },
            600: {
                items: 4
            },
            1000: {
                items: 7
            }
        }
    });

}

let shopCart = [];

function toyItemAddHandler({
    name,
    price,
    id,
    img
}) {
    document.querySelector('.cart-image').disabled = false;

    /**
     * TOY ITEM OBJECT{
     * @param price
     * @param name
     * @param toyId
     * @param inCart - counter
     * @param img -img url
     */

    const cartItem = {
        price: price,
        name: name,
        toyId: id,
        inCart: 1,
        img: img
    };

    //** CHECK IF CURRENT TOY ITEM IS ALREADY EXIST */ 
    if (shopCart.some((toyItem) => toyItem.toyId === id)) {
        toyItemExistInShopCart(id);
    } else {
        shopCart.push(cartItem);
    }
    shopCartProperties();
    renderShopCartItems();
    cartPopup(cartItem, {
        popupType: 'add'
    });
}

function toyItemExistInShopCart(id) {
    const cartItemIndexToIncrease = shopCart.findIndex(toyItem => toyItem.toyId === id);
    shopCart[cartItemIndexToIncrease].inCart = shopCart[cartItemIndexToIncrease].inCart + 1;
}

// SHOP CART
// SHOP CART DISABLED
const shopCartHandler = document.querySelector('.cart-image');
const shopCartContainer = document.querySelector('.cart-block');
shopCartHandler.addEventListener('click', () => {
    shopCartContainer.classList.toggle('show');
});

function emptyCart() {
    if (!shopCart.length) {
        shopCartHandler.disabled = true;
        shopCartContainer.classList.remove('show');
    }
}
// END SHOP CART DISABLED

// RENDER SHOP CART TOTAL PRICE AND ITEMS
function shopCartProperties() {

    const shopCartCounter = document.querySelector('.cart-items-counter');
    let inCartTotalItems = shopCart.reduce((inCart, toy) => {
        return inCart + toy.inCart
    }, 0);
    shopCartCounter.textContent = inCartTotalItems;

    let shopCartTotalPrice = shopCart.reduce((total, toy) => {
        return total + (toy.price * toy.inCart)
    }, 0);
    const shopCartPrice = document.querySelector('.cart-items-total-price');
    shopCartPrice.textContent = `${shopCartTotalPrice.toFixed(2)} zł`;

}
// END RENDER SHOP CART TOTAL PRICE AND ITEMS


function renderShopCartItems() {

    shopCartContainer.innerHTML = '';

    if (!shopCart.length) {
        emptyCart();
    } else {
        shopCart.map(toyItem => {
            // CART TOY ITEM BODY
            const cartToyItem = document.createElement('div');
            cartToyItem.classList.add('cart__item');

            // CART TOY ITEM IMG
            const cartToyItemImg = document.createElement('img');
            cartToyItemImg.classList.add('cart__item-img');
            cartToyItemImg.src = `${toyItem.img}`;
            //END CART TOY ITEM  IMG

            // CART TOY ITEM PRICE
            const cartToyItemPrice = document.createElement('p');
            cartToyItemPrice.classList.add('cart__item-price');
            cartToyItemPrice.textContent = `${(toyItem.price * toyItem.inCart).toFixed(2)}`;
            //END CART TOY ITEM PRICE

            // CART TOY ITEM NAME
            const cartToyItemName = document.createElement('p');
            cartToyItemName.classList.add('cart__item-name');
            cartToyItemName.textContent = toyItem.name;
            //END CART TOY ITEM NAME

            // CART TOY ITEM INCART COUNTER
            const toyCartItemInCart = document.createElement('p');
            toyCartItemInCart.classList.add('cart__item-incart');
            toyCartItemInCart.textContent = toyItem.inCart;
            // END CART TOY ITEM INCART COUNTER

            // CART TOY ITEM DELETE BUTTON
            const toyItemCartDeleteButton = document.createElement('button');
            toyItemCartDeleteButton.classList.add('cart__item-delete');
            toyItemCartDeleteButton.innerHTML = `<img src="img/icons/delete.png">`;
            toyItemCartDeleteButton.addEventListener('click', () => {
                deleteItemFromCartHandler(toyItem);
            });
            // END CART TOY ITEM DELETE BUTTON/
            // END TOY CART ITEM BODY

            // RENDER TOY ITEM IN CART
            cartToyItem.append(cartToyItemImg, cartToyItemName, cartToyItemPrice, toyCartItemInCart, toyItemCartDeleteButton);
            shopCartContainer.appendChild(cartToyItem);
        });
    }

}
// END SHOP CART

// POPUP TEMPLATE
function cartPopup({
    img,
    name,
    price,
    toyId,
    inCart
}, {
    popupType
}) {
    // POPUP BODY
    const popup = document.createElement('div');
    popup.classList.add('popup');
    if (popup.classList.contains('popup')) {
        document.body.style.overflowY = 'hidden';
    }

    popup.addEventListener('click', ({
        target
    }) => {
        if (target.classList.contains('popup')) {
            target.parentNode.removeChild(popup);
            document.body.style.overflowY = 'auto';
        }

    });
    // END POPUP BODY

    const popupWrapper = document.createElement('div');
    popupWrapper.classList.add('popup__wrapper');

    // POPUP STATUS
    const popupText = document.createElement('p');
    popupType === 'add' ? popupText.classList.add('popup__wrapper-status') : popupText.classList.add('popup__wrapper-status', 'delete');
    popupType === 'add' ? popupText.textContent = 'Dodano do koszyka ' : popupText.textContent = 'Usunąć z kosza? ';
    // POPUP STATUS

    // POPUP ITEM IMG
    const popupItemImg = document.createElement('img');
    popupItemImg.src = `${img}`;
    //END POPUP ITEM IMG

    // POPUP ITEM NAME
    const popupItemName = document.createElement('p');
    popupItemName.classList.add('popup__wrapper-name');
    popupItemName.textContent = `${name}`;
    // END POPUP ITEM NAME
    // POPUP ITEM PRICE
    const popupItemPrice = document.createElement('p');
    popupItemPrice.classList.add('popup__wrapper-price');
    popupItemPrice.textContent = `${(price * inCart).toFixed(2)} zł`;
    //END POPUP ITEM PRICE

    //END POPUP ITEM PRICE
    // POPUP CLOSE BUTTON
    const popupCloseButton = document.createElement('button');
    popupCloseButton.classList.add('popup__wrapper-close');
    popupCloseButton.textContent = 'X';
    popupCloseButton.addEventListener('click', ({
        currentTarget
    }) => {
        currentTarget.parentNode.parentNode.remove();
        document.body.style.overflowY = 'auto';
    });
    // POPUP CLOSE BUTTON

    // POPUP BOTTOM BUTTON WRAPPER
    const popupButtonWrapper = document.createElement('div');

    if (popupType === 'add') {

        const popupCartButton = document.createElement('button');
        popupCartButton.classList.add('popup__wrapper-cart', 'btn');
        popupCartButton.textContent = 'Wyśwetl koszyk';
        popupCartButton.addEventListener('click', ({
            currentTarget
        }) => {
            currentTarget.parentNode.parentNode.parentNode.remove();
            document.body.style.overflowY = 'auto';
            shopCartContainer.scrollIntoView({
                behavior: "smooth",
                block: "end",
                inline: "nearest"
            });
            shopCartContainer.classList.add('show');

        });
        popupButtonWrapper.append(popupCartButton);
    } else {
        const popupCartButtonDelete = document.createElement('button');
        popupCartButtonDelete.classList.add('popup__wrapper-cart', 'btn', 'error');
        popupCartButtonDelete.textContent = 'Usunąć';
        popupCartButtonDelete.addEventListener('click', function (event) {
            cartItemToDelete({
                toyId
            }, event);
        });
        const popupCartButtonSave = document.createElement('button');
        popupCartButtonSave.classList.add('popup__wrapper-cart', 'btn', 'save');
        popupCartButtonSave.textContent = 'Zostawić';
        popupCartButtonSave.addEventListener('click', ({
            currentTarget
        }) => {
            currentTarget.parentNode.parentNode.parentNode.remove();
            document.body.style.overflowY = 'auto';
        });

        popupButtonWrapper.append(popupCartButtonSave, popupCartButtonDelete);
    }
    // END POPUP BOTTOM BUTTON WRAPPER
    popupWrapper.append(popupText, popupItemImg, popupItemName, popupItemPrice, popupButtonWrapper, popupCloseButton);
    popup.appendChild(popupWrapper);
    document.body.appendChild(popup);

}
// END POPUP TEMPALTE

// DELETE ITEM
function deleteItemFromCartHandler(cartItem) {
    cartPopup(cartItem, {
        popupType: 'delete',
    });
}

function cartItemToDelete({
    toyId
}, {
    currentTarget
}) {
    currentTarget.parentNode.parentNode.parentNode.remove();
    document.body.style.overflowY = 'auto';
    const cartItemToDelete = shopCart.findIndex(item => item.toyId === toyId);
    shopCart.splice(cartItemToDelete, 1);
    renderShopCartItems();
    shopCartProperties();
}
// END DELETE ITEM

// SEARCH
function searchToysHandler(shopToys) {
    const toySearchInput = document.getElementById('toy-search');
    toySearchInput.addEventListener('keyup', (event) => {
        searchHandler(shopToys, event);
    });
}

// SEARCH HANDLER BY TOY NAME
function searchHandler(globalToysArray, event) {
    const serachingToy = event.target.value.trim().toLocaleLowerCase();
    const searchingToy = globalToysArray.filter(toy => {
        return toy.name.trim().toLocaleLowerCase().includes(serachingToy)
    });
    recomendedToysContainer.innerHTML = '';
    bestsellerToysContainer.innerHTML = '';
    renderToysCard(searchingToy, serachingToy);

}
// END SEARCH

const DATA_URL = "https://skyshoprecruitmentproject.firebaseio.com/";
const recomendedToysContainer = document.querySelector(
  ".recommended-toys"
);
const bestsellerToysContainer = document.querySelector(
  ".bestseller-toys"
);

function fetchShopProducts() {
  axios
    .get(`${DATA_URL}/toys.json`)
    .then((result) => {
      renderToysCard(result.data);
      searchToysHandler(result.data);
    })
    .catch((error) => console.log("error", error));
}

function renderToysCard(toys, serachingToy) {
  const bestsellerToys = toys.filter(
    (toyItem) => toyItem.option === "bestseller"
  );
  if (!bestsellerToys.length) {
    searchResultEmpty(bestsellerToysContainer, serachingToy);
  } else {
    bestsellerToys.map((toy) => {
      toyCardTemplate(toy);
    });
  }

  const recommendedToys = toys.filter(
    (toyItem) => toyItem.option === "polecamy"
  );
  if (!recommendedToys.length) {
    searchResultEmpty(recomendedToysContainer, serachingToy);
  } else {
    recommendedToys.map((toy) => {
      toyCardTemplate(toy);
    });
  }
}

// TOY CARD TEMPLATE
function toyCardTemplate(toy) {
  // TOY BOYD
  const toyCard = document.createElement("div");
  toyCard.classList.add("toy__card");
  // TOY HEADER
  const toyCardHeader = document.createElement("div");
  toyCardHeader.classList.add("toy__card-header");

  const toyCardHeaderOption = document.createElement("p");
  toyCardHeaderOption.classList.add("toy__card-header-option");
  toyCardHeaderOption.textContent = toy.option;

  const toyCardOptionDiscount = document.createElement("span");
  toyCardOptionDiscount.classList.add("toy__card-header-discount");
  toy.discount
    ? (toyCardOptionDiscount.textContent = toy.discount)
    : (toyCardOptionDiscount.style.display = "none");
  // END TOY HEADER

  // TOY CARD IMG
  const toyCardImg = document.createElement("img");
  toyCardImg.classList.add("toy__card-img");
  toyCardImg.src = `${toy.img}`;
  // END TOY CARD IMG

  // TOY CARD NAME
  const toyCardName = document.createElement("p");
  toyCardName.classList.add("toy__card-name");
  toyCardName.textContent = toy.name;
  // END TOY CARD NAME
  const toyCardPrice = document.createElement("span");
  toyCardPrice.classList.add("toy__card-price");
  toyCardPrice.textContent = `${toy.price.toFixed(2)} zł`;

  // TOY CARD OLD PRICE
  const toyCardOldPrice = document.createElement("span");
  toyCardOldPrice.classList.add("toy__card-price-old");
  toy.oldPrice
    ? (toyCardOldPrice.textContent = `${toy.oldPrice.toFixed(2)} zł`)
    : (toyCardOldPrice.style.display = "none");
  // END TOY CARD OLD PRICE

  // TOY CARD BUTTON CONTAINER
  const toyCartButtonWrapper = document.createElement("div");
  toyCartButtonWrapper.classList.add("toy__card-option-wrapper");
  // END TOY CARD CONTAINER

  // TOY CARD BUTTON OPTIONS
  const toyCardButtonAddItem = document.createElement("button");
  toyCardButtonAddItem.classList.add("toy__card-option-wrapper-button", "add");
  toyCardButtonAddItem.innerHTML = `<img src="img/icons/shopping-cart.png"> do koszyka`;
  toyCardButtonAddItem.addEventListener("click", () => {
    toyItemAddHandler(toy);
  });

  const toyCardButtonSeeItem = document.createElement("button");
  toyCardButtonSeeItem.classList.add("toy__card-option-wrapper-button", "see");
  toyCardButtonSeeItem.innerHTML = `<img src="img/icons/eye.png">`;

  const toyCardButtonLikeItem = document.createElement("button");
  toyCardButtonLikeItem.classList.add(
    "toy__card-option-wrapper-button",
    "like"
  );
  toyCardButtonLikeItem.innerHTML = `<img src="img/icons/like.png">`;
  toyCardButtonLikeItem.addEventListener("click", ({ currentTarget }) => {
    currentTarget.classList.toggle("active");
  });
  // END TOY CARD BOTTUN OPTIONS
  // END TOY BODY

  // RENDERING TOY CARD
  toyCartButtonWrapper.append(
    toyCardButtonAddItem,
    toyCardButtonSeeItem,
    toyCardButtonLikeItem
  );
  toyCardHeader.append(toyCardHeaderOption, toyCardOptionDiscount);
  toyCard.append(
    toyCardHeader,
    toyCardImg,
    toyCartButtonWrapper,
    toyCardName,
    toyCardPrice,
    toyCardOldPrice
  );

  if (toy.option === "bestseller") {
    bestsellerToysContainer.append(toyCard);
  } else if (toy.option === "polecamy") {
    recomendedToysContainer.append(toyCard);
  }
  // END RENDERING TOY CARD
}
// END TOY CARD TEMPLATE

// SEARCH RESULT
function searchResultEmpty(containerToRender, serachingToy) {
  const emptySearchResultMessage = document.createElement("p");
  emptySearchResultMessage.classList.add("search-result");
  emptySearchResultMessage.innerHTML = `nie znaleziono zabawki pod takim tytułem <span>'${serachingToy}'</span></>`;
  containerToRender.append(emptySearchResultMessage);
}
// END SEARCH RESULT

// STICKY HEADER

function pageScrollHandler() {
    const pageHeader = document.querySelector('header');
    window.addEventListener('scroll', () => {
        const scrolledY = window.scrollY;
        if (scrolledY >= 20) {
            pageHeader.classList.add('header-scrolled');
        } else {
            pageHeader.classList.remove('header-scrolled');
        }
        if (pageHeader.classList.contains('header-scrolled')) {
            shopCartContainer.classList.add('sticked');
        } else {
            shopCartContainer.classList.remove('sticked');
        }
    });
}
// END STICKY HEADER

document.addEventListener("DOMContentLoaded", function () {
	fetchShopProducts();
	slidersInit();
	shopCartProperties();
	pageScrollHandler();
});

}());
